<template>
    <div class="create-main-page">
        <el-card class="create-mian" shadow="hover">
            <el-form :model="post_data" label-width="80px" class="create-form">
                <el-form-item label="标题" prop="title">
                    <el-input 
                        class="title" 
                        v-model="post_data.title" 
                        type="textarea" 
                        :rows="3" 
                        placeholder="请输入标题"
                        maxlength="100"
                        show-word-limit
                    ></el-input>
                </el-form-item>
                <el-divider></el-divider>

                <el-form-item label="摘要" prop="summary">
                    <el-input 
                        class="summary" 
                        v-model="post_data.summary" 
                        type="textarea" 
                        :rows="5" 
                        placeholder="请在此处输入文章摘要"
                        maxlength="200"
                        show-word-limit
                    ></el-input>
                </el-form-item>
                <el-divider></el-divider>
                <!-- 正文 -->
                <el-form-item label="正文" prop="content">
                    <el-input 
                        class="content" 
                        v-model="post_data.content" 
                        type="textarea" 
                        :rows="20" 
                        placeholder="请输入内容"
                        maxlength="5000"
                        show-word-limit
                    ></el-input>
                </el-form-item>
                <!-- 评论区设置 -->
                <el-form-item label="评论区">
                    <el-switch 
                        v-model="post_data.comments_status" 
                        active-text="开启" 
                        inactive-text="关闭"
                        active-color="#1989fa"
                        inactive-color="#F56C6C"
                    ></el-switch>
                </el-form-item>

                <el-form-item>
                    <el-button 
                        type="primary" 
                        @click="Send" 
                        :disabled="isSending"
                        style="width: 100%;"
                    >
                        {{ isSending ? '提交中...' : '提交' }}
                    </el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { ref} from 'vue';
import {post} from '@/http/httpRequest'
import { useUrl } from '@/store/useUrl';
import { notice } from './notice';

export default {
    setup(){

        const urls = useUrl();
        const isSending = ref(false);

        const post_data = ref({
            title: '',
            content: '',
            comments_status: true,
            summary:"",
            action: "create"
        });

        const Send=async ()=>{
            if (post_data.value.title.length<=0) {
                notice("错误", '标题不能为空');
                return;
            }

            if (post_data.value.content.length < 50) {
                notice("错误", '内容字数不低于50');
                return;
            }

            isSending.value = true;
            let url = urls.create_post;
            let data = post_data.value;
            const response= await post(url, data);

            if(response) notice("审核成功","",2000); else notice("审核失败","",2000);
            
            post_data.value.title = '';
            post_data.value.content = '';
            post_data.value.summary='';
            isSending.value = false; 
        }
        return {
            Send,
            post_data,
            isSending,
        }
    }
}
</script>

